import React, {PureComponent, Fragment} from 'react';
import Switch from 'ic-switch';
import Markdown from 'ic-markdown';
import switchMD from './switch.md';

function onChange(checked) {
    console.log(`switch to ${checked}`);
}

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section>开关切换</section>
                <section>
                    <Switch onChange={onChange}/> 最简单的用法，只指定变化的回调函数<br />
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="已开启" uncheckedLabel="已关闭" /> 带有文字的开关<br />
                    <Switch onChange={onChange} disabled={true} /> 初始选中的失效状态<br />
                    <Switch onChange={onChange} defaultChecked={false} disabled /> 初始未选中的失效状态
                </section>
                <section>
                    <Markdown>{switchMD}</Markdown>
                </section>
            </Fragment>
        );
    }
}




